.main {
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    background-color: $white;
    .row {
        flex-direction: column;
        justify-content: center;
        h2 {
            font-size: 3rem;
            line-height: 4.2rem;
            font-weight: 500;
            z-index: 10;
            color: $white;
            mix-blend-mode: difference;
            @include media("<=tablet") {
                font-size: 2.4rem;
                line-height: 3rem;
            }
            @include media("<=phone") {
                margin-top: 72px;
                font-size: 1.7rem;
                line-height: 2.2rem;
            }
            .line {
                margin-bottom: 4px;
                height: 62px;
                position: relative;
                overflow: hidden;
                @include media("<=tablet") {
                    height: 48px;
                }
                @include media("<=phone") {
                    height: 32px;
                }
                span {
                    position: absolute;
                }
            }
        }
        .btn-row {
            width: 256px;
            position: relative;
            z-index: 2;
            a {
                font-size: 1.6rem;
                color: $black;
                text-decoration: none;
                display: flex;
                align-items: center;
                font-weight: 600;
                @include media("<=tablet") {
                    font-size: 1.3rem;
                    padding-bottom: 150px;
                }
                @include media("<=phone") {
                    font-size: 1.1rem;
                    padding-bottom: 250px;
                }
                svg {
                    margin-left: 16px;
                    border: 2px solid $black;
                    padding: 12px;
                    height: 24px;
                    width: 24px;
                    border-radius: 100px;
                    transition: 0.4s ease-in-out;
                    @include media("<=tablet") {
                        padding: 6px;
                        width: 20px;
                        height: 20px;
                    }
                    @include media("<=phone") {
                        padding: 4px;
                        width: 16px;
                        height: 16px;
                    }
                }
                &:hover {
                    svg {
                        background: $black;
                        color: $white;
                    }
                }
            }
        }
    }
    .background-video {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 100%;
        max-height: 100%;
        pointer-events: none;
        @include media("<=phone") {
            object-fit: cover;
            width: 200%;
        }
    }
}
